import { UI } from './ui';
import { Render } from './render';

document.addEventListener("DOMContentLoaded", () => { 
    UI.init();
    UI.nav.bind();
    UI.tabs.bind();
} );

window.addEventListener('resize', () => { UI.init() } );

/** 
 * Render React microfrontends components
 */
 Render.posts('.posts-list', wp_core.rest.posts);